.App {
  display: flex;
  flex-direction: column;
}

.App-Header {
  background-color: #1f79f1;
  width: 100%;
  height: 40px;
  display: flex;
  align-items: center;
}

.Header-Divider {
  width: 1px;
  height: 75%;
  background-color: #f7f9f9;
}

.Makerhub-Header {
  color: #ffffff;
  text-decoration: none;
  margin: 20px;
}

.App-Nav {
  display: flex;
}

.App-Nav ul {
  margin: 0px;
  padding: 0px;
  list-style-type: none;
  display: flex;
  justify-content: space-evenly;
}

.Nav-Link {
  color: #ffffff;
  text-decoration: none;
  padding: 5px;
  margin-left: 15px;
}

.Nav-Link:hover {
  background-color: #0a369d;
  border-radius: 10px;
}

.App-Content {
  display: flex;
  width: 100%;
}

.App-Sidebar {
  display: flex;
  flex-direction: column;
  background-color: #eee;
  min-width: 300px;
  height: calc(100vh - 40px);
}

.Connection {
  display: flex;
  flex-direction: column;
  margin-top: 20px;
  margin-left: 20px;
}

.Connection label {
  padding: 10px;
  display: flex;
  flex-direction: column;
}

.Connection label select {
  margin-top: 5px;
  border: none;
  padding: 10px;
}

.Connection select:disabled {
  color: #757575;
}

.Connect,
.Disconnect {
  margin-top: 20px;
  width: 80%;
  align-self: center;
  border: none;
  background-color: #fefefe;
  border-radius: 3px;
  padding: 10px;
  transition-duration: 0.25s;
}

.Connect:hover {
  color: #ffffff;
  background-color: #1f79f1;
}

.Connect:active {
  background-color: rgb(45, 48, 145);
  color: #ffffff;
}

.Connect:disabled {
  background-color: #aeaeae;
  color: #eeeeee;
}

.Disconnect:hover {
  background-color: #f34e54;
  color: #ffffff;
}

.Disconnect:active {
  background-color: #780116;
  color: #ffffff;
}

.Guages {
  display: flex;
  margin: 20px;
}

.Guages > div {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.Guages input {
  width: 30px;
  text-align: center;
  border: none;
  border-bottom: 1px solid #000000;
  font-size: 17px;
}

.Aside-Divider {
  height: 1px;
  width: 90%;
  background-color: #ccc;
  margin: 20px;
  align-self: center;
}

.Files {
  height: 100%;
}

.Files-Upload {
  display: flex;
  margin-left: 20px;
  margin-bottom: 10px;
  justify-content: center;
  border-radius: 3px;
}

.Files-Upload span {
  width: 80%;
  height: 35px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #fefefe;
  font-size: 13.33px;
  border-radius: 3px;
  transition-duration: 0.25s;
}

.Files-Upload span:hover {
  color: #ffffff;
  background-color: #1f79f1;
}

.Files-Upload span:active {
  background-color: rgb(45, 48, 145);
  color: #ffffff;
}

.Files-Upload input {
  display: none;
}

.Files-List {
  background-color: #fefefe;
  height: 60%;
  width: 100%;
  overflow: scroll;
  margin: 0px;
  padding: 0px;
  list-style-type: none;
}

.Files-List li button {
  width: 100%;
  padding: 10px;
  border: none;
  border-bottom: 1px solid #eeeeee;
  background-color: #fefefe;
  transition-duration: 0.25s;
}

.Files-List li button:hover {
  background-color: #1f79f1;
  border-bottom: 1px solid #1f79f1;
  color: #ffffff;
}

.Files-List li button:active {
  background-color: rgb(45, 48, 145);
  color: #ffffff;
}

.Files-List li button:disabled {
  background-color: #fefefe;
  color: #757575;
  border-bottom: 1px solid #eeeeee;
}

.Active-File {
  background-color: #1f79f1 !important;
  color: #ffffff;
}

.Files-Actions {
  list-style-type: none;
  display: flex;
  margin: 0px;
  padding: 0px;
}

.Files-Actions li {
  width: 50%;
}

.Files-Actions button:disabled {
  background-color: #aeaeae;
  color: #eeeeee;
}

.Files-Print {
  border: none;
  background-color: #fefefe;
  width: 100%;
  padding: 10px;
  transition-duration: 0.25s;
}

.Files-Print:hover {
  background-color: #1f79f1;
  color: #ffffff;
}

.Files-Print:active {
  background-color: rgb(45, 48, 145);
  color: #ffffff;
}

.Files-Delete {
  border: none;
  background-color: #fefefe;
  width: 100%;
  padding: 10px;
}

.Files-Delete:hover {
  background-color: #f34e54;
  color: #ffffff;
}

.Files-Delete:active {
  background-color: #780116;
  color: #ffffff;
}

.Jog {
  display: flex;
  flex-direction: column;
  margin: 20px;
  box-shadow: 0px 0px 5px black;
}

.Controls {
  display: flex;
  height: fit-content;
}

.Jog svg {
  font-size: 20px;
}

.Jog button {
  background-color: #eeeeee;
  border: none;
  transition-duration: 0.2s;
  border-right: 1px solid #757575;
  border-bottom: 1px solid #757575;
  padding: 10px;
}

.Jog button:hover {
  background-color: #1f79f1;
  color: #ffffff;
}

.Jog button:active {
  background-color: rgb(45, 48, 145);
  color: #ffffff;
}

.Jog button:disabled {
  background-color: #eeeeee;
  color: #757575;
}

.Controls-XY {
  display: flex;
  flex-direction: column;
}

.Controls-X {
  display: flex;
}

.Controls-Z {
  display: flex;
  flex-direction: column;
}

.Controls-Z button {
  height: 50%;
  border-right: none;
}

.Controls-Feed {
  display: flex;
}

.Controls-Feed button {
  width: 100%;
  border-bottom: none;
}

.Controls-Feed button:last-child {
  border-right: none;
}

.Feed-Active {
  background-color: #1f79f1 !important;
  color: #ffffff;
}

.Console-Container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.Printer-Status {
  display: flex;
  flex-direction: row-reverse;
  width: 100%;
  justify-content: center;
  align-items: center;
}
