.Console-Content {
  width: 70vw;
  display: flex;
  flex-direction: column;
}

.Console-Content ul {
  list-style-type: none;
  margin: 0px;
  padding: 0px;
  background-color: #eee;
  width: 100%;
  height: 75vh;
  overflow-y: scroll;
  display: flex;
  flex-direction: column;
  border-bottom: 1px solid #ddd;
}

.Console-Content li {
  margin-top: 10px;
  margin-left: 20px;
}

.Console-Input {
  background-color: #eee;
  width: 100%;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.Console-Input input {
  border: none;
  width: 95%;
  height: 75%;
  border-radius: 5px;
  padding-left: 10px;
}
